.price {
  color: @main;
}

.price__title {
  font-size: 18px;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.price__table {
  width: 100%;
  border: 3px solid @main;
  border-collapse: collapse;
  margin-bottom: 20px;

  @media (min-width: @desktop-width) {
    font-size: 16px;
  }
}

.price__head-row {
  border-bottom: 1px solid @main;
}

.price__head-cell {
  text-transform: uppercase;
  background-color: @base;
  padding: 5px;
}

.price__body-row {
  border-bottom: 1px solid @secondary;
}

.price__body-cell {
  padding: 5px;

  &:not(:first-child) {
    text-align: center;
    font-weight: bold;
  }

  @media (min-width: @tablet-width) {
    padding: 10px;
  }

  @media (min-width: @desktop-width) {
    padding: 10px 20px;
  }
}

.price__name {
  color: @main;
  margin: 0;
  padding: 0;

  & span {
    font-weight: bold;
  }
}
