.page-inner {
  flex: 1 1 auto;
  color: @main;
}

.inner__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.inner__header {
  @media (min-width: @tablet-width) {
    display: flex;
  }
}

.inner__title {
  font-size: 18px;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.inner__image {
  display: block;
  width: 100%;

  @media (min-width: @tablet-width) {
    max-width: 50%;
  }
}

.inner__deskription {
  margin: 0;
  padding: 10px 0;

  @media (min-width: @tablet-width) {
    padding: 20px;
  }
}

.inner__table {
  width: 100%;
  border-collapse: collapse;
  text-align: center;
  margin-bottom: 20px;

  @media (min-width: @desktop-width) {
    font-size: 16px;
  }
}

.inner__head {
  font-size: 12px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
  }
}

.inner__head-cell {
  text-transform: uppercase;
  background-color: @base;
  padding: 5px;

  &:nth-child(3n) {
    min-width: 65px;
  }
}

.inner__body-row {
  border-bottom: 1px solid @secondary;
}

.inner__body-cell {
  text-align: center;
  padding: 8px 0;

  @media (min-width: @tablet-width) {
    padding: 10px;
  }
}
