.work-examples__title {
  color: @main;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.work-examples__wrapper {
  color: @main;
  margin-bottom: 20px;

  @media (min-width: @desktop-width) {
    margin-bottom: 30px;
  }
}

.work-examples__info {
  border-top: 1px solid @secondary;
  border-bottom: 1px solid @secondary;
  padding: 10px 0;

  @media (min-width: @tablet-width) {
    padding: 30px 0;
    font-size: 16px;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
    margin-bottom: 30px;
  }
}

.work-examples__list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
  }
}

.work-examples__item {
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}

.work-examples__image {
  display: block;
  width: 100%;
}
