.demand__title {
  color: @main;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 28px;
  }
}

.demand__info {
  color: @main;
  font-size: 16px;
  font-weight: bold;
  border-top: 1px solid @secondary;
  border-bottom: 1px solid @secondary;
  padding: 10px 0;

  & span {
    color: @base;
  }

  @media (min-width: @tablet-width) {
    text-align: center;
    font-size: 18px;
    padding: 30px 0;
  }

  @media (min-width: @desktop-width) {
    font-size: 20px;
    margin-bottom: 30px;
  }
}

.demand__wrapper {
  color: @main;
}

.demand__list {
  list-style: square;
  padding: 0 0 0 20px;
  margin-bottom: 25px;

  @media (min-width: @tablet-width) {
    margin-bottom: 30px;
  }
}

.demand__item-title {
  font-weight: bold;
  color: @base;
  list-style: none;
  margin-left: -20px;
}

.demand__item-title,
.demand__item {
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    margin-bottom: 15px;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
  }
}
